.profile {
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 260px;
    height: 350px;
    background-color: rgb(250, 249, 245);
    border: 2px solid rgb(207, 209, 204);
    border-radius: 5px;
    box-shadow: 0px 4px 11px 4px rgba(59, 53, 53, 0.58);
}

.description {
    display: flex;
    align-items: center;
    flex-direction: column;
    margin: 30px 0;
}

.avatar {
    border-radius: 50%;
    width: 100px;
    padding: 10px;
    background-color: #fff;
}

.name,
.tag,
.location {
    margin: 6px;
}

.name {
    font-size: 20px;
    font-weight: 700;
    color: #000;
}

.tag,
.location {
    font-size: 16px;
    font-weight: 600;
    color: gray;
}

.label {
    font-size: 14px;
    font-weight: 500;
    color: gray;
}

.quantity {
    font-size: 18px;
    font-weight: 700;
    padding-top: 4px;
    color: #000;
}

.stats {
    border-radius: 5px;
   
}

.stats,
.statsItem {
    display: flex;
    justify-content: center;
    align-items: center;
    text-decoration: none;
    list-style: none;
    margin: 0;
    padding: 8px 0;
    width: 100%; 
    height: 100%; 
    background-color: rgba(241, 243, 237, 0.968);
    border: 1px solid rgb(207, 209, 204);
}

.statsItem {
    width: calc(100% / 3);
    flex-direction: column;
}